body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar{
    display: flex;
    flex: 1 1;
    position: fixed;
    width: 100%;
    padding: .75rem 1rem;
    z-index: 1;
    background-color: rgba(255,255,255,.25);
}

#menu{
    display: flex;
    flex: 1 1;
    justify-content: flex-end;
    padding-right: 2rem;
}

#menu .sub-menu{
    padding: 0 .75rem;
}

#menu .sub-menu a{
    text-decoration: none;
    font-size: 1.5rem;
    font-weight: bold;
    border-bottom: .15rem solid transparent;
    -webkit-transition: all .2s;
    transition: all .2s;
    cursor: pointer;
}

#menu .sub-menu:hover a{
    border-bottom-color: black;
}

.change-color{
    opacity: .5;
    padding: .25rem;
    -webkit-transition: all .2s;
    transition: all .2s;
    cursor: pointer;
}

.change-color:hover{
    opacity: 1;
}

.change-color span{
    margin-left: .25rem;
    font-size: .75rem;
}
.full-screen-page{
    height: 100vh;
    position: relative;
    display: flex;
    flex: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.fa-social-icons{
    display: inline-block;
    width: 2rem;
    height: 2rem;
    padding: 1rem;
    margin: .5rem;
    font-size: 2rem;
    border-radius: 50%;
    background-color: #777;
    color: #fff;
    -webkit-transition: all .2s;
    transition: all .2s;
}

.fa-social-icons:hover{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    box-shadow: 0 0 .5rem .15rem #eee;
}

.fa-social-icons.telegram-plane{
    background-color: #3498db;
}

.fa-social-icons.telegram-plane:hover{
    background-color: #fff;
    color: #3498db;
}

.fa-social-icons.instagram{
    background-color: #8a3ab9;
}

.fa-social-icons.instagram svg{
    -webkit-transform: translateX(2px) translateY(-2px);
            transform: translateX(2px) translateY(-2px);
}

.fa-social-icons.instagram:hover{
    background-color: #fff;
    color: #8a3ab9;
}

.fa-social-icons.stack-overflow{
    background-color: #f48024;
}

.fa-social-icons.stack-overflow svg{
    -webkit-transform: translateX(3px) translateY(-3px);
            transform: translateX(3px) translateY(-3px);
}


.fa-social-icons.stack-overflow:hover{
    background-color: #fff;
    color: #f48024;
}

.fa-social-icons.github{
    background-color: #333;
}

.fa-social-icons.github svg{
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
}

.fa-social-icons.github:hover{
    background-color: #fff;
    color: #333;
}
.scroll-down {
    text-align: center;
    position: absolute;
    bottom: .25rem;
    font-size: 2.75rem;
    cursor: pointer;
}

.scroll-down span {
    display: block;
    font-size: .75rem;
    position: relative;
    top: .35rem;
}

.scroll-down.bounce {
    -webkit-animation: bounce 2s infinite;
    animation: bounce 2s infinite;
}

@-webkit-keyframes bounce {
    0%,
    20%,
    50%,
    80%,
    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    40% {
        -webkit-transform: translateY(-30px);
        transform: translateY(-30px);
    }
    60% {
        -webkit-transform: translateY(-15px);
        transform: translateY(-15px);
    }
}

@keyframes bounce {
    0%,
    20%,
    50%,
    80%,
    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    40% {
        -webkit-transform: translateY(-30px);
        transform: translateY(-30px);
    }
    60% {
        -webkit-transform: translateY(-15px);
        transform: translateY(-15px);
    }
}
.title{
    font-size: 5rem;
    margin: 0;
}

.subtitle{
    text-transform: uppercase;
    letter-spacing: .25rem;
    color: #bdc3c7;
}
.second .title{
    font-size: 4rem;
    margin-bottom: 5rem;
}
.card-parent{
    padding: 1rem .75rem
}

.card{
    text-align: center;
    text-transform: uppercase;
    background-color: #fff;
    color: #bdc3c7;
    border-radius: 1rem;
    box-shadow:0 .125rem .5rem rgba(0,0,0,.075);
    -webkit-transition: all .2s;
    transition: all .2s;
}

.card h3{
    margin: 0;
    padding: 1rem;
}

.card .image-wrapper{
    border-radius: 1rem 1rem 0 0;
    overflow: hidden;
}

.card:hover{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    box-shadow:0 .125rem 1.25rem rgba(0,0,0,.075);
    color: #000;
    color: initial;
}

.card .image-wrapper img{
    border-radius: 1rem 1rem 0 0;
    -webkit-transition: all .2s;
    transition: all .2s;
    width: 20rem;
}

.card:hover .image-wrapper img{
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
}


.third .title{
    font-size: 4rem;
    margin-bottom: 5rem;
}

.third .card-container{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}
html, body {
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Raleway', sans-serif;
}

